// IMPORTS
// Material UI
import { Grid } from '@material-ui/core'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import PropTypes from 'prop-types'

// Externals imports (Dependencies)
import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import { Link, Route } from 'react-router-dom'
import { bindActionCreators } from 'redux'

// Internal imports
// import EditStore from './Edit'
// import List from './List'
// import NewStore from './New'
// import ListDistributionCenters from './distributioncenters/index'
// import styles from '../../../resources/theme/stores'

class DistributionCentersStores extends Component {
  componentDidMount() {
    this.props.fetchStores()
  }

  render() {
    const { classes, items } = this.props

    return (
      <Fragment>
        <Grid container justify="center" alignItems="center">
          <Grid item className={classes.grow}>
            <Typography variant="h3">Centros de Distribuição de Loja X</Typography>
          </Grid>
        </Grid>

        <Route path="/stores/new" component={NewStore} />
        <Route path="/stores/:id/edit" component={EditStore} />
        <Route path="/stores/:id/distributioncenters" component={ListDistributionCenters} />

        <List classes={classes} items={items} />
      </Fragment>
    )
  }
}

List.propTypes = {
    classes: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired
}

const mapStateToProps = state => ({
    items: state.distributioncenter.items_store
})

const mapDispatchToProps = dispatch =>
bindActionCreators(
    {
    fetchDistributionCentersPerStores,
    saveDistributionCentersStore
    },
    dispatch
)
  

// export Store class
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(DistributionCentersStores))
